@import 'variables';
body {
    margin: 0;
    padding: 0;
    #header {
        background-color: $grey;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", "Open Sans", Arial, sans-serif;
        .header-wrapper {
            max-width: 1200px;
            width: 100%;
            height: 80px;
            padding: 0 15px;
            margin: 0 auto;
            box-sizing: border-box;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .header-logo-wrapper {
                display: flex;
                align-items: center;
                img {
                    max-height: 60px;
                    width: auto;
                }
            }
            .burger-menu {
                display: none;
            }
            .menu-wrapper {
                display: flex;
                align-items: center;
                .header-menu {
                    display: flex;
                    ul.menu {
                        display: flex;
                        list-style: none;
                        padding-left: 0;
                        margin: 0;
                        .menu-item {
                            padding: 0 25px 0 14px;
                            height: 80px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            transition: all 0.3s;
                            position: relative;
                            flex-direction: column;
                            a{
                                color: $tawny-port;
                                text-decoration: none;
                                text-transform: uppercase;
                                font-size: 13px;
                                font-weight: 500;
                                &::before {
                                    content: '';
                                    margin-top: 6px;
                                    position: absolute;
                                    right: 7px;
                                    border: 4px solid transparent;
                                    border-top: 4px solid $wine-berry;
                                }
                                @media (max-width: 1100px) {
                                    &::before {
                                        display: none;
                                    }
                                }
                                .fa-angle-down {
                                    display: none;
                                }
                            }
                            .sub-menu {
                                display: none;
                            }
                            &:hover{
                                background-color: $wine-berry;
                                .sub-menu {
                                    display: block;
                                    position: absolute;
                                    padding: 15px;
                                    width: 230px;
                                    box-sizing: border-box;
                                    top: 100%;
                                    left:0;
                                    background-color: $grey;
                                    border-top: 1px solid $wine-berry;
                                    z-index: 10;
                                    li {
                                        height: auto;
                                        padding: 0;
                                        display: flex;
                                        flex-direction: column;
                                        align-items: flex-start;
                                        background-color: transparent;
                                        a {
                                            color: $tawny-port;
                                            padding: 8px 10px;
                                            font-weight: 400;
                                            &:before {
                                                display: none;
                                            }
                                        }
                                        &:hover {
                                            a {
                                                color: $wine-berry;
                                            }
                                        }
                                    }
                                }
                                a {
                                    color: $white;
                                    &::before {
                                        border-top: 4px solid $white;
                                    }
                                }
                            }
                            &:last-child:hover {
                                .sub-menu {
                                    left:-137%;
                                }
                            }
                        }
                        @media (max-width: 1100px) {
                            .menu-item {
                                padding: 0 7px;
                            }
                        }
                        .menu-item.home-item {
                            padding: 0 20px;
                            a {
                                font-size: 21px;
                                &::before {
                                    border: none;
                                }
                            }
                        }
                        @media (max-width: 1070px) {
                            .menu-item a {
                                font-size: 12px;
                            }
                        }
                    }
                    .search {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        width: 30px;
                        color: $tawny-port;
                        i {
                            cursor: pointer;
                        }
                    }
                    .mobile-search {
                        display: none;
                    }
                }
            }
            .close-button-burger {
                display: none;
            }
            @media(max-width: 991px) {
                .burger-menu {
                    display: flex;
                    align-items: center;
                    width: 20px;
                    height: 16px;
                    position: relative;
                    &:hover {
                        &:after,
                        &:before,
                        span {
                            background-color: #7c284e;
                        }
                    }
                    &:after,
                    &::before {
                        content: "";
                        background-color: #2c2f34;
                        position:absolute;
                        width: 100%;
                        height: 2px;
                        left: 0;
                    }
                    &:after {
                        top: 0;
                    }
                    &::before {
                        bottom: 0;
                    }
                    & span {
                        position: absolute;
                        height: 2px;
                        top: 7px;
                        width: 100%;
                        background-color: #2c2f34;
                    }

                }
                .menu-wrapper {
                    right: -400%;
                    position: fixed;
                    transition: all 1s;
                    width: 100%;
                    height: 100%;
                }
                .menu-wrapper.active {
                    position: fixed;
                    transition: all 0.6s;
                    width: 100%;
                    height: 100%;
                    top: 0;
                    right: 0;
                    z-index: 1;
                    background-color: #1f2024;
                    align-items: flex-start;
                    scroll-behavior: auto;
                    overflow: scroll;
                    .header-menu {
                        margin-top: 70px;
                        padding: 10px 30px;
                        box-sizing: border-box;
                        width: 100%;
                        flex-direction: column;
                        .menu {
                            flex-direction: column;
                            margin-bottom: 20px;
                            li.menu-item {
                                height: auto;
                                padding: 15px 50px 15px 0;
                                border-bottom: 1px solid rgba(255,255,255,0.1);
                                align-items: flex-start;
                                a {
                                    color: $white;
                                    font-size: 16px;
                                    font-weight: 600;
                                    .fa-angle-down {
                                        display: inline-block;
                                        position: absolute;
                                        right: 12px;
                                        font-size: 20px;
                                        pointer-events: none;
                                    }
                                    &:before {
                                        display: none;
                                    }
                                }
                                &:hover {
                                    background-color: transparent;
                                    a {
                                        transition: 0.3s all;
                                        opacity: 0.7;
                                        color: $grey;
                                    }
                                    .sub-menu {
                                        display: block;
                                        position: static;
                                        display: block;
                                        width: 100%;
                                        border: none;
                                        background-color: transparent;
                                        li {
                                            a {
                                                color: white;
                                                opacity: 1;
                                                &:before {
                                                    display: none;
                                                }
                                            }
                                            &:hover {
                                                a {
                                                    color: $grey;
                                                    opacity: 0.7;
                                                }
                                            }
                                        }
                                    }
                                }
                            }
                            li.menu-item.home-item {
                                padding: 15px 50px 15px 0;
                            }
                        }
                        .search {
                            display:none;
                        }
                        .mobile-search {
                            display: block;
                            position: relative;
                            padding: 0;
                            margin: 0;
                            list-style: none;
                            border: 0;
                            outline: none;
                            box-sizing: border-box;
                            .search-mobile {
                                position: relative;
                                width: 100%;
                                margin-top: 10px;
                                .search-field {
                                    background-color: rgba(0,0,0,0.05);
                                    border-color: rgba(255,255,255,0.1);
                                    color: $white;
                                    width: 100%;
                                    border-radius: 50px;
                                    padding: 7px 45px 7px 20px;
                                    min-height: 40px;
                                    box-sizing: border-box;
                                    &:focus-visible {
                                        outline: none;
                                    }
                                }
                                .search-submit {
                                    background: transparent;
                                    font-size: 20px;
                                    border: none;
                                    color: $white;
                                    width: 35px;
                                    height: 38px;
                                    position: absolute;
                                    top: 0;
                                    right: 10px;
                                    z-index: 1;
                                    vertical-align: middle;
                                }
                            }
                        }
                    }
                    .close-button-burger {
                        display: block;
                        position: absolute;
                        right: 50%;
                        top:15px;
                        color: $white;
                        transition: 0.3s all;
                        font-size: 22px;
                        &:hover {
                            color: red;
                        }
                    }
                }
            }
        }
    }
    @media (max-width: 991px) {
        #header{
           box-shadow: 0px -30px 50px 0px #000000;
        }
    }
    .search-popup {
        display: none;
        &.active {
            display: block;
            position: fixed;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: rgba(0,0,0,0.9);
            .close-button-search {
                position: absolute;
                right: 15px;
                top:15px;
                color: $white;
                transition: 0.3s all;
                font-size: 22px;
                z-index: 10;
                &:hover {
                    color: red;
                }
            }
            .search-form-wrapper {
                position: relative;
                padding: 80px 15px 0;
                margin: 0 auto;
                max-width: 800px;
                .search-form {
                    position: relative;
                    height: 80px;
                    margin-bottom: 20px;
                    .search-input {
                        max-width: 770px;
                        width: 100%;
                        height: 80px;
                        line-height: 80px;
                        background: rgba(0,0,0,0.7);
                        font-weight: 500;
                        font-size: 22px;
                        border: none;
                        padding-right: 80px;
                        padding-left: 30px;
                        color: $white;
                        box-sizing: border-box;
                        &:focus-visible {
                            outline: none;
                        }
                    }
                    .search-submit {
                        font-size: 28px;
                        color: #666;
                        position: absolute;
                        right: 5%;
                        top: 20px;
                        background: transparent;
                        transition: 0.15s;
                        border: none;
                        padding: 7px 15px;
                        cursor: pointer;
                        vertical-align: middle;
                    }
                    @media(max-width: 500px) {
                        .search-input {
                            font-size: 17px;
                        }
                    }
                }
            }
        }
    }
}